import axios from 'axios';

const apiClient = axios.create({
  // baseURL: 'https://api-yamsdrive.srv557357.hstgr.cloud', // Ancienne URL de production
  baseURL: '/api', // Toutes les requêtes seront préfixées par /api et gérées par le proxy
  withCredentials: true, // Crucial pour l'authentification SPA avec Sanctum
  headers: {
    'Accept': 'application/json',
  }
});

// Intercepteur pour ajouter le token d'authentification à chaque requête
apiClient.interceptors.request.use(config => {
  const sessionStr = localStorage.getItem('yamsdigital_session');
  if (sessionStr) {
    try {
      const session = JSON.parse(sessionStr);
      if (session && session.data && session.data.token) {
        config.headers.Authorization = `Bearer ${session.data.token}`;
      }
    } catch (error) {
      console.error('Erreur lors de la lecture de la session depuis localStorage:', error);
    }
  }
  return config;
}, error => {
  return Promise.reject(error);
});

// Intercepteur de réponse pour gérer les comptes gelés
apiClient.interceptors.response.use(
  response => response,
  error => {
    // Si le backend retourne une erreur 403 avec statut compte_suspendu
    if (error.response?.status === 403 && error.response?.data?.statut === 'compte_suspendu') {
      // Nettoyer la session
      localStorage.removeItem('yamsdigital_session');
      localStorage.removeItem('auth_token');
      
      // Rediriger vers la page de compte suspendu
      window.location.href = '/account-suspended';
    }
    
    return Promise.reject(error);
  }
);

export default apiClient;
