const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// Guard de navigation pour vérifier si le compte est gelé
router.beforeEach((to, from, next) => {
  // Ignorer la vérification pour les pages publiques
  const publicPages = ['/connexion', '/account-suspended', '/inscription'];
  const isSharedDocument = to.path.startsWith('/shared/');
  
  if (publicPages.includes(to.path) || isSharedDocument) {
    next();
    return;
  }
  
  // Récupérer la session de l'utilisateur
  const sessionData = localStorage.getItem('yamsdigital_session');
  
  if (sessionData) {
    try {
      const session = JSON.parse(sessionData);
      const user = session?.data?.user;
      
      // Vérifier si l'utilisateur est gelé
      if (user?.is_frozen) {
        // Rediriger vers la page de compte suspendu
        next('/account-suspended');
        return;
      }
    } catch (error) {
      console.error('Erreur lors de la vérification du statut du compte:', error);
    }
  }
  
  next();
})

export default router
